export default {
  name: "DynamicImport",
  methods: {
    load: async function () {
      let say = await import("@/common/say.js");
      say.hi(); // Hello!
      say.bye(); // Bye!
      say.default(); // Module loaded (export default)!
    },
  },
};
</script>